import React from 'react'
import './carsdetail.css'
import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';




function CarsDetails() {
  return (
    <div className='car-detail_body'>
      <div className="filter-bar_detail">

      </div>
      <div className="car-detail_container">
        <div className="car_photos">
          <div className="car_banner">
            <h2 className="car-upper_ad">Sports car with the best design and acceleration</h2>
            <h3 className="car-lower_ad">Safety and comfort while driving a futuristic and elegant sports car</h3>
            <img src="" alt="" className="car_img" />
          </div>
          <div className="car_small-photos">
            <img src="" alt="" className="car-small_img" />
            <img src="" alt="" className="car_decoration-1" />
            <img src="" alt="" className="car_decoration-2" />
            
          </div>
        </div>
        <div className="car_description-detail">
          <h2 className="car_name-detail">Nissan GT-R</h2>
          <div className="review_description"></div>
          <div className="main_description">
            <div className="text_description">NISMO has become the embodiment of Nissan's outstanding performance, inspired by the most unforgiving proving ground, the "race track".</div>
            <div className="detailed_description"></div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default CarsDetails