import React from 'react'
import { useState, useEffect } from 'react'
import './filterbar.css'
import CarsData from './CarsData'
import Checkbox from '@mui/material/Checkbox';
import Slider from '@mui/material/Slider';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import LocalGasStationIcon from '@mui/icons-material/LocalGasStation';
import PeopleAltIcon from '@mui/icons-material/PeopleAlt';
import FiberManualRecordIcon from '@mui/icons-material/FiberManualRecord';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Stack from '@mui/material/Stack';
import TextField from '@mui/material/TextField';
import FavoriteBorder from '@mui/icons-material/FavoriteBorder';
import Favorite from '@mui/icons-material/Favorite';
import RadioButtonUncheckedIcon from '@mui/icons-material/RadioButtonUnchecked';
import RadioButtonCheckedIcon from '@mui/icons-material/RadioButtonChecked';
import { type } from '@testing-library/user-event/dist/type';
import { useNavigate } from 'react-router-dom'
import { eachWeekOfInterval } from 'date-fns/esm';




function FilterBar() {

  const label = { inputProps: { 'aria-label': 'Checkbox demo' } };

  const [heart, setHeart] = useState(false)
  const handleHeart = () => {
    setHeart(!heart)
  }



  // Filter Logic
  const [data, setData] = useState(CarsData)
  const [filterArray, setFilterArray] = useState([])
  const [filterCapacityArray, setFilterCapacityArray] = useState([])

  const filterItems = {
    typeOfCars: filterArray,
    numOfPeople: filterCapacityArray
  }

  console.log(filterItems)


  // const filterItemsArray = [filterArray, filterCapacity]

  // console.log(filterItemsArray)

  useEffect(() => {
    console.log(filterArray)
  }, [filterArray])



  // useEffect(() => {
  //   console.log(filterCapacityArray)
  // }, [filterCapacityArray])


  const filterType = (filType) => {
    const typeIncluded = filterArray.includes(filType)

    if (typeIncluded) {
      setFilterArray(filterArray.filter((x) => {
        return x !== filType
      }))


    } else {
      setFilterArray(filterArray.concat(filType))

    }
  }


  const handleResetFilter = () => {
    setData(CarsData)
    setFilterArray([])
    setFilterCapacityArray([])
  }





  // Handle Capacity Logic
  const filterCapacity = (filCapa) => {
    const capacityIncluded = filterCapacityArray.includes(filCapa)
    if (capacityIncluded) {
      setFilterCapacityArray(filterCapacityArray.filter(x => x !== filCapa))
    } else {
      setFilterCapacityArray(filterCapacityArray.concat(filCapa))
    }
  }


  useEffect(() => {
    if (filterItems.typeOfCars.length === 0) {
      setData(CarsData)
    } else {
      const temp = CarsData.filter((e) => filterItems.typeOfCars.includes(e.type))
      setData(temp)
    }
  }, [filterArray])








  return (
    <div className='filterBar'>
      <div className="filter_container">
        <div className="type">
          <h3 className='h3_type'>TYPE</h3>
          <div>
            <Checkbox
              checked={filterArray.includes('Sport')}
              {...label} size="small" id='sport' onChange={() => filterType('Sport')} />
            <label htmlFor="sport">Sport</label>
          </div>
          <div>
            <Checkbox
              checked={filterArray.includes('SUV')}
              {...label} size="small" id='SUV' onChange={() => filterType('SUV')} />
            <label htmlFor="SUV">SUV</label>
          </div>
          <div>
            <Checkbox
              checked={filterArray.includes('MPV')}
              {...label} size="small" id='MPV' onChange={() => filterType('MPV')} />
            <label htmlFor="MPV">MPV</label>
          </div>
          <div>
            <Checkbox
              checked={filterArray.includes('Sedan')}
              {...label} size="small" id='sedan' onChange={() => filterType('Sedan')} />
            <label htmlFor="sedan">Sedan</label>
          </div>
          <div>
            <Checkbox
              checked={filterArray.includes('Coupe')}
              {...label} size="small" id='Coupe' onChange={() => filterType('Coupe')} />
            <label htmlFor="Coupe">Coupe</label>
          </div>
          <div>
            <Checkbox
              checked={filterArray.includes('Hatchback')}
              {...label} size="small" id='Hatchback' onChange={() => filterType('Hatchback')} />
            <label htmlFor="Hatchback">Hatchback</label>
          </div>
        </div>
        <div className="capacity">
          <h3 className="h3_capacity">CAPACITY</h3>
          <div>
            <Checkbox
              checked={filterCapacityArray.includes(2)}
              {...label} size="small" id='2 Person' onChange={() => filterCapacity(2)} />
            <label htmlFor="2 Person">2 Person</label>
          </div>
          <div>
            <Checkbox
              checked={filterCapacityArray.includes(4)}
              {...label} size="small" id='4 Person' onChange={() => filterCapacity(4)} />
            <label htmlFor="4 Person">4 Person</label>
          </div>
          <div>
            <Checkbox
              checked={filterCapacityArray.includes(6)}
              {...label} size="small" id='6 Person' onChange={() => filterCapacity(6)} />
            <label htmlFor="6 Person">6 Person</label>
          </div>
          <div>
            <Checkbox
              checked={filterCapacityArray.includes(8)}
              {...label} size="small" id='8 Person' onChange={() => filterCapacity(8)} />
            <label htmlFor="8 Person">8 Person</label>
          </div>
        </div>
        <div className="price">
          <h3 className="h3_price">PRICE</h3>
          <Box sx={{ width: 300 }}>
            <Slider
              aria-label="Always visible"
              defaultValue={100}
              step={10}
              valueLabelDisplay="on"
              max={150}

            />
          </Box>
          <h3 className="price_description">Max. $100.00 </h3>
        </div>

        <Button variant="contained" onClick={handleResetFilter} className='reset_btn'>
          Reset Filter
        </Button>
      </div>

      {/* Body */}

      <div className="body_container">
        <div className="row_1">
          <div className="pick_up">
            <Checkbox
              {...label}
              icon={<RadioButtonUncheckedIcon />}
              checkedIcon={<RadioButtonCheckedIcon />}
              id='pickup_check'
            />
            <label htmlFor='pickup_check' className='top_text'>Pick-Up</label>
            <Box sx={{ minWidth: 40 }} className=''>
              <FormControl fullWidth>
                <InputLabel id="demo-simple-select-label">Locations</InputLabel>
                <Select
                  labelId="demo-simple-select-label"
                  id="demo-simple-select"
                  label="Locations"

                >
                  <MenuItem value={10}>Ha Noi</MenuItem>
                  <MenuItem value={20}>HCM</MenuItem>
                  <MenuItem value={30}>Da Nang</MenuItem>
                </Select>
              </FormControl>
            </Box>
            <div className="date_time">
              <h4 className="date_description">Date</h4>
              <input type="datetime-local" name="" id="" />
              <h4 className="time_description">Time</h4>
              <input type="time" name="" id="" />
            </div>
          </div>
          <div className="drop_off">
            <Checkbox
              {...label}
              icon={<RadioButtonUncheckedIcon />}
              checkedIcon={<RadioButtonCheckedIcon />}
              id='dropoff_check'
            />
            <label htmlFor='dropoff_check' className='top_text'>Drop-Off</label>
            <Box sx={{ minWidth: 40 }} className=''>
              <FormControl fullWidth>
                <InputLabel id="demo-simple-select-label">Locations</InputLabel>
                <Select
                  labelId="demo-simple-select-label"
                  id="demo-simple-select"
                  label="Locations"
                >
                  <MenuItem value={'HaNoi'}>Ha Noi</MenuItem>
                  <MenuItem value={'HCM'}>HCM</MenuItem>
                  <MenuItem value={'Da Nang'}>Da Nang</MenuItem>
                </Select>
              </FormControl>
            </Box>
            <div className="date_time">
              <h4 className="date_description">Date</h4>
              <input type="datetime-local" name="" id="" />
              <h4 className="time_description">Time</h4>
              <input type="time" name="" id="" />
            </div>
          </div>
        </div>

        {/* Cars */}

        <div className="row_2">
          {filterItems.numOfPeople.length === 0 ? (
            data.map((values) => {
              const { id, name, type, price, img, fuel, engine, capacity } = values
              return (
                <>
                  <div className="cars_containers" key={id}>
                    <h2 className="car_name">{name}</h2>
                    <h3 className="car-type_single">{type}</h3>
                    <div className="toggle_heart">
                      <Checkbox {...label} icon={<FavoriteBorder />} checkedIcon={<Favorite />} />
                    </div>
                    <img src={img} alt="" className='sportcar_img' />
                    <div className="car_description">
                      <div className="fuel">
                        <LocalGasStationIcon className='fuel' />
                        <h4 className="fuel_des">{fuel} L</h4>
                      </div>
                      <div className="engine">
                        <FiberManualRecordIcon />
                        <h4 className="engine_des">{engine}</h4>
                      </div>
                      <div className="capacity">
                        <PeopleAltIcon />
                        <h4 className="capacity">{capacity} People</h4>
                      </div>
                    </div>
                    <div className="renting_price">
                      <span className='car_price'>${price} /day</span>
                      <div className="renting-btn_div">
                        <Button variant="contained" className='renting_btn' disableElevation>
                          Rent Now
                        </Button>
                      </div>
                    </div>
                  </div>
                </>
              )
            })
          ) : (

            <>
              {
                data.filter(el => filterItems.numOfPeople.includes(el.capacity))
                  .map((values) => {
                    const { id, name, type, price, img, fuel, engine, engine_icon, capacity } = values
                    return (
                      <>
                        <div className="cars_containers" key={id}>
                          <h2 className="car_name">{name}</h2>
                          <h3 className="car-type_single">{type}</h3>
                          <div className="toggle_heart">
                            <Checkbox {...label} icon={<FavoriteBorder />} checkedIcon={<Favorite />} />
                          </div>
                          <img src={img} alt="" className='sportcar_img' />
                          <div className="car_description">
                            <div className="fuel">
                              <LocalGasStationIcon className='fuel' />,
                              <h4 className="fuel_des">{fuel} L</h4>
                            </div>
                            <div className="engine">
                              <FiberManualRecordIcon />
                              <h4 className="engine_des">{engine}</h4>
                            </div>
                            <div className="capacity">
                              <PeopleAltIcon />
                              <h4 className="capacity">{capacity} People</h4>
                            </div>
                          </div>
                          <div className="renting_price">
                            <span className='car_price'>${price} /day</span>
                            <div className="renting-btn_div">
                              <Button variant="contained" className='renting_btn' disableElevation>
                                Rent Now
                              </Button>
                            </div>
                          </div>
                        </div>
                      </>
                    )
                  })
              }
            </>)}
        </div>
      </div>
    </div>


  )
}


export default FilterBar